import { useEffect, useState } from "react";
import moment from "moment";
import InfiniteScroll from "react-infinite-scroll-component";
import Container from "react-bootstrap/Container";
import Row from "react-bootstrap/Row";
import Col from "react-bootstrap/Col";

export default function List() {
  const [selectedOpt, setSelectedOpt] = useState("1");
  const [resultData, setResultData] = useState([]);
  const [totalCount, setTotalCount] = useState(0);
  const [page, setPage] = useState(1);

  // const [open, setOpen] = useState(false);

  useEffect(() => {
    getdata();
  }, [selectedOpt]);
  let selectedDate = "";
  const getdata = () => {
    // let selectedDate = "";
    if (selectedOpt === "1") {
      selectedDate = moment().subtract(7, "days").format("YYYY-MM-DD");
    } else if (selectedOpt === "2") {
      selectedDate = moment().subtract(14, "days").format("YYYY-MM-DD");
    } else {
      selectedDate = moment().subtract(1, "months").format("YYYY-MM-DD");
    }

    console.log(selectedDate);

    let url = `https://api.github.com/search/repositories?q=created:>${selectedDate}&sort=stars&order=desc&page=${page}`;

    console.log(url);

    fetch(url)
      .then((res) => res.json())
      .then((json) => {
        console.log(json);
        setPage(page + 1);

        setResultData([
          ...resultData,
          ...json?.items?.map((item) => {
            return { ...item, open: false };
          })
        ]);

        setTotalCount(json.total_count);
      });
  };

  const changeMet = (index) => {
    let tempData = resultData.map((data, idx) => {
      if (index === idx) {
        console.log(index, idx);
        let url = `https://api.github.com/search/repositories?q=created:>${selectedDate}&sort=stars&order=desc&page=${page}`;

        console.log(url);

        fetch(url)
          .then((res) => res.json())
          .then((json) => {
            console.log("testttttttttttttttt");
            console.log(json);
            // setPage(page + 1);

            setTotalCount(json.total_count);
          });
        return { ...data, open: !data.open };
      } else {
        return { ...data, open: false };
      }
    });

    setResultData(tempData);

    // ðŸ‘‡ï¸ {one: false, two: false, three: false}
    // console.log(tempData);
  };

  const onchangeDD = (e) => {
    console.log(e.target.value);
    setSelectedOpt(e.target.value);
  };
  return (
    <div className="App">
      <select onChange={(e) => onchangeDD(e)} value={selectedOpt}>
        <option value="1">1 week</option>
        <option value="2">2 week</option>
        <option value="3">1 month</option>
      </select>
      <div>
        <InfiniteScroll
          dataLength={totalCount}
          next={() => getdata()}
          hasMore={true}
          loader={<h4>Loading...</h4>}
        >
          {resultData.map((item, index) => (
            <div key={index}>
              <br></br>
              <div style={{ border: "1px solid rgba(0, 0, 0, 0.05)" }}>
                <Container>
                  <Row>
                    <Col>
                      <div
                        style={{
                          display: "inline",
                          marginRight: "50%",
                          paddingRight: "50%"
                        }}
                      >
                        <img
                          src={item.owner.avatar_url}
                          width={80}
                          height={80}
                          alt=""
                        />
                      </div>
                    </Col>
                    <Col>
                      <Row>{item.name}</Row>
                      <Row>{item.description}</Row>
                      <Row>{item.score}</Row>
                      <Row>{item.open_issues_count}</Row>
                      <button onClick={() => changeMet(index)}>
                        {" "}
                        aero img
                      </button>
                      {item.open === true && <div>aero down</div>}
                    </Col>
                  </Row>
                </Container>
              </div>
              {/* <div>Repository name: {item.name}</div> */}
              {/* <br></br>
              <br></br>
              <div>Repository description: {item.description}</div>
              <br></br>
              <br></br>
              <div>Number of stars : {item.score}</div>
              <br></br>
              <br></br>
              <div>Number of issues: {item.open_issues_count}</div>
              <br></br>
              <br></br>
              <div>Username : {item.owner.login}</div>
              <br></br>
              <br></br>
              <button onClick={() => changeMet(index)}> aero img</button>
              {item.open === true && <div>aero down</div>} */}
            </div>
          ))}
        </InfiniteScroll>
      </div>
    </div>
  );
}
